// 1. Cho chng ti bit v trang web ca bn
  // Cho chng ti bit URL ca trang web ca bn l g.

// 2. Thit lp SDK Facebook dnh cho JavaScript
  // Facebook SDK dnh cho JavaScript khng c bt k tp c lp no cn ti xung hay ci t,
  // thay vo , bn ch cn a mt on ngn ca JavaScript thng thng vo HTML s ti
  // khng ng b SDK vo cc trang ca bn.Ti khng ng b ngha l khng chn ti cc phn t khc ca trang.

// 3. Kim tra trng thi ng nhp
  // status ch nh trng thi ng nhp ca ngi dng ng dng. Trng thi c th l:
  // connected - ngi   ng nhp Facebook v ng nhp ng dng ca bn.
  // not_authorized - ngi   ng nhp Facebook nhng khng ng nhp ng dng ca bn.
  // unknown - ngi  khng ng nhp Facebook nn bn khng bit h c ng nhp ng dng
  // ca mnh khng hoc FB.logout()  c gi trc  v do vy, khng th kt ni vi Facebook.

  // C authResponse nu trng thi l connected v bao gm cc yu t sau:
  // accessToken - bao gm mt m truy cp cho ngi dng ng dng.
  // expiresIn - cho bit thi gian UNIX khi m ht hn v cn c gia hn.
  // signedRequest - mt thng s c nh du bao gm thng tin v ngi dng ng dng.
  // userID - ID ca ngi dng ng dng.

  // Sau khi ng dng bit trng thi ng nhp ca ngi dng, ng dng  c th thc hin mt trong nhng iu sau:
  // Nu ngi  ng nhp vo Facebook v ng dng ca bn, hy chuyn h n tri nghim ng nhp ca ng dng.
  // Nu ngi  khng ng nhp ng dng ca bn hoc khng ng nhp Facebook, hy nhc h bng hp thoi
  // ng nhp bng FB.login() hoc hin th cho h nt ng nhp.
  
// 4. Thm nt ng nhp Facebook
  // Vic thm nt ng nhp vo trang rt n gin. Hy truy cp ti liu v nt ng nhp v thit lp nt theo 
  // cch bn mun.Sau  nhp vo Nhn m v bn s nhn thy m mnh cn  hin th nt ny trn trang.
  // Thuc tnh onlogin trn nt  thit lp lnh gi li JavaScript kim tra trng thi ng nhp  xem ngi 
  //   ng nhp thnh cng cha:
  // <fb:login-button
  //   scope="public_profile,email"
  //   onlogin="checkLoginState();">
  // </fb:login-button>Sao chp m
  // y l lnh gi li. Lnh ny gi FB.getLoginStatus()  nhn trng thi ng nhp gn y nht. 
  // (statusChangeCallback() l hm thuc mt phn ca v d x l phn hi.)
  // function checkLoginState() {
  //   FB.getLoginStatus(function(response) {
  //     statusChangeCallback(response);
  //   });
  // }
  
function fn() {
  const statusChangeCallback = ({ status, authResponse }) => {
    if (status === "not_authorized") {// not_authorized | unknown
      console.log('You are not logged in', authResponse);
    } else {//connected => FB.logout()
      console.log('You are logged in', authResponse);
    }
  }

  if (FB && typeof FB.getLoginStatus === 'function') {
    FB.getLoginStatus(function(response) {
      statusChangeCallback(response);
    });
  }
}

window.startWorkingWithFBAPI = fn;
